// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import {db, au} from '../../../Config/firebase'
import {FaTimes, FaPlus, FaCheck, FaUserAlt, FaUserAltSlash} from 'react-icons/fa'

const AboutComponent = styled.div`
	width: 100%;
	min-height: calc(100vh - 421.4px);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px;
`
const Delete = styled(FaTimes)`
	position: absolute;
	top: -10px;
	right: -10px;
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 25px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
`
const Add = styled(FaPlus)`
	position: absolute;
	bottom: -20px;
	left: 50%;
	transform: translateX(-50%);
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 25px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
`
const Image = styled.div`
	position: relative;
	width: 90vw;
	max-width: 1000px;
	height: 300px;
	margin: 0 0 30px 0;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	@media (min-width: 540px) {
		height: 400px;
		margin: 10px 0 30px 0;
	}
	:hover ${Delete}, :focus ${Delete},
	:hover ${Add}, :focus ${Add} {
		opacity: 1;
	}
`
const Title = styled.h1`
	position: relative;
	display: block;
	margin: 10px 0;
	outline: none;
	:hover ${Delete}, :focus ${Delete},
	:hover ${Add}, :focus ${Add} {
		opacity: 1;
	}
`
const Letter = styled.span`
	color: var(--color-main);
`
const Paragraph = styled.p`
	position: relative;
	max-width: 1000px;
	margin: 20px 0;
	text-align: center;
	outline: none;
	:hover ${Delete}, :focus ${Delete},
	:hover ${Add}, :focus ${Add} {
		opacity: 1;
	}
`
const BrowserLink = styled.a`
	color: var(--color-main);
	font-weight: bold;
	text-decoration: none;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			opacity: 0.7;
		}
	}
`
const Save = styled(FaCheck)`
	font-size: 25px;
	color: var(--color-main);
`
const View = styled.div`
	position: fixed;
	bottom: 30px;
	left: 30px;
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-bg);
	color: var(--color-main);
	border-radius: 100%;
	font-size: 20px;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			opacity: 0.7;
		}
	}
`

class About extends Component {
	state = {
		isChange: false,
		content: null,
		isAdmin: false,
		adminView: false
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true, adminView: true})
					}
				})
			}
			else {
				this.setState({
					isAdmin: false
				})
			}
		})
		db.collection('pages').doc('about').get().then(snap => {
			this.setState({content: Object.values(snap.data().content)})
		})
	}
	componentDidUpdate() {
		document.querySelectorAll('.editable').forEach(element => {
			element.addEventListener('paste', e => {
				e.preventDefault();
				const text = (e.originalEvent || e).clipboardData.getData('text/plain');
				document.execCommand("insertHTML", false, text);
			})
		})
	}
	delete = (num) => {
		console.log(num);
	}
	add = (num) => {
		console.log(num)
	}
	addChanges = (e, index) => {
		if (!this.state.isChange) {
			this.setState({isChange: true})
		}
		let link;
		let text;
		e.currentTarget.innerHTML.replace(/<a.+<\/a>/g, x => {
			link = x.replace('<a href="', '').replace(/".+/, '')
			text = x.replace(/<a.+">/, '').replace('</a>', '')
		});
		console.log(link, text);
		this.state.content[index].text = e.currentTarget.innerText.replace(text, `--${text}(${link})--`)
	}
	save = () => {
		if (this.state.isChange) {
			this.setState({isChange: false})
		}
		if (this.state.content) {
			const content = {}
			this.state.content.forEach((element, index) => {
				content[index.toString()] = element
			})
			db.collection('pages').doc('about').update({
				content: content
			})
		}
	}
	changeView = () => {
		this.setState(prevState => ({
			adminView: !prevState.adminView
		}));
	}
	render() {
		return (
			<AboutComponent>
				{!this.state.content ? '...' : this.state.content.map(({type, text, src, href}, index) => {
					if (type === 'image') {
						return <Image src={src} id={index} key={index}>
							{this.state.adminView && <>
								<Delete onClick={() => this.delete(index)} />
								<Add onClick={() => this.add(index)} />
							</>}
						</Image>
					}
					else if (type === 'title') {
						return <Title contentEditable={this.state.adminView} onInput={(e) => this.addChanges(e, index)} suppressContentEditableWarning={true} className="editable" id={index} key={index}>
							{this.state.adminView && <>
								<Delete onClick={() => this.delete(index)} />
								<Add onClick={() => this.add(index)} />
							</>}
							{this.state.adminView ? text : text.split('').map((letter) => /^[A-Z]*$/.test(letter) ? <Letter>{letter}</Letter> : letter)}
						</Title>
					}
					else if (type === 'paragraph') {
						return <Paragraph contentEditable={this.state.adminView} onInput={(e) => this.addChanges(e, index)} suppressContentEditableWarning={true} className="editable" id={index} key={index}>
							{this.state.adminView && <>
								<Delete onClick={() => this.delete(index)} />
								<Add onClick={() => this.add(index)} />
							</>}
							{this.state.adminView ? text : text.split('--').map((fragment) => {
								if (fragment.includes('http')) {
									const text = fragment.replace(/\(.+\)/, '')
									const link = fragment.replace(/.+\(/, '').replace(')', '')
									return <BrowserLink href={link} target="_blank">{text}</BrowserLink>
								}
								else {
									return fragment
								}
							})}
						</Paragraph>
					}
				})}
				{this.state.adminView && this.state.isChange && <Save onClick={this.save} />}
				{this.state.isAdmin && <>
					{this.state.adminView ? <View onClick={this.changeView} title="Zmień na widok użytkownika"><FaUserAlt /></View> : <View onClick={this.changeView} title="Zmień na widok administratora"><FaUserAltSlash /></View>}
				</>}
			</AboutComponent>
		);
	}
}

export default About